@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-nav:hover {
  background-color: #9CA3AF;
  background-size:auto;
  height:10px;
  opacity:0.5;
  border-radius:10px;
}

.span {
 font-weight:bold;
 font-size:38px;
}

.homeBotton {
 background-color:black;
 width:80px;
 height:80px;
 border-radius:100%;
 color:white;
 font-size:12px;
 justify-content:center;
}

.homeBotton:hover {
  opacity:0.5;
}

.clickButton{
  background-color:black;
  color:white;
  margin-top:30px;
  font-size:20px;
  width:80px;
  height:35px;
  border-radius:10%;
}

.clickButton:hover {
  opacity:0.5;
}


@media screen and (max-width: 1000px) {
	
  .resheaderimg{
    height:900px;
  }
  .resheadername{
  position:absolute;
  top:400px;
  left:140px;
  }
  .resheaderlist{
  flex-wrap:wrap;
  margin-left:15px;
  }

  .resmenulist{
  padding-top:30px;
  }

  .resprofilelist{
    position:relative;
    right:60px;
  }

  .restoplikeslist{
    position:relative;
    right:110px;
  }

  .reslikeslist{
    margin-bottom:100px;
  }

  .rescarrercontent{
    width:400px;
  }

}

